<template>
    <div id="MessageTemplateContent">
        <el-card class="box-card" id="MessageTemplateCard" shadow="always">
            <div slot="header" class="clearfix">
                        <span>
                            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                            &nbsp;&nbsp;&nbsp;&nbsp;
                            消息模板
                        </span>
                <el-button style="float: right; padding: 3px 0; margin-left: 15px" type="text" v-on:click="pushTemplateData()">推送</el-button>
                <el-button style="float: right; padding: 3px 0;" type="text" v-on:click="pullTemplateData()">拉取</el-button>

            </div>
            <div>
                <el-input
                    type="textarea"
                    :autosize="{ minRows: 10, maxRows: 200}"
                    placeholder="请输入Golang语法的模板内容"
                    v-model="textarea"
                    resize="none"
                >
                </el-input>
            </div>
        </el-card>
    </div>

</template>

<script>
export default {
    name: "MessageTemplate",
    data() {
        return {
            textarea: '点击拉取按钮，获取示例模板',
        }
    },
    methods: {
        printData: function () {
            console.log(this.textarea);
        },
        //一键粘贴（纯原生js document对象实现）
        copyCode: function () {
            const val = document.getElementById('templateContent');
            // let val = this.textarea;
            window.getSelection().selectAllChildren(val);
            document.execCommand("Copy");
            alert("已复制好，可以贴粘...");
        },
        handlePaste(e) {
            let clip = e.clipboardData.getData('Text');
            this.text = clip;
            console.log(clip);
        },
        //推送数据
        pushTemplateData: function () {

            console.log(this.textarea);

            this.axios.post('/web/template',
                {
                    "message_template": this.textarea,
                }
            )
                .then(response => {
                    // alert("模板已存储在库中...");
                    console.log(response.data);
                })
                .catch(err => {
                    console.log(err);
                });
        },
        //拉取数据
        pullTemplateData: function () {
            this.axios.get('/web/template')
                .then(response => {
                    this.textarea = response.data["MessageTemplate"];
                    console.log(response.data);
                })
                .catch(err => {
                    console.log(err);
                });
        }
    }, created() {
        this.pullTemplateData();
    }
}
</script>

<style scoped>
#MessageTemplateContent {
    width: 60%;
    margin-left: 20%;
}

.templateContentClass >>> input {
    background-color: #2c3e50;
